.timeline {
    position:       relative;
    display:        flex;
    flex-direction: row;
    flex-wrap:      wrap;
    margin-right:   1rem;
    /* border: 1px solid black; */
}

/* vertical bar */
.timeline::after {
    content:    '';
    height:     calc(100% - 3.2rem);
    position:   absolute;
    top:        2.5rem;
    left:       100%;
    width:      1px;
    background: var(--primary-aux-color);
    box-shadow: 0px 0px 3px 1px rgba(15, 160, 138, 0.3);
}

.timeline-item {
    /* border: 1px solid black; */
    display:         flex;
    flex-direction:  row;
    flex-wrap:       wrap;
    justify-content: space-between;
    align-items:     center;
    line-height:     20px;
    font-weight:     400;
    border-radius:   0.2rem;
    position:        relative;
    padding:         0.5rem;
    margin:          0.5rem 1rem;
    margin-right:    1.5rem;
    box-shadow:      rgba(50, 50, 93, 0.25) 0px 2px 5px -1px,
    rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

/* top border */
.timeline-item::before {
    content:                 '';
    position:                absolute;
    left:                    0;
    top:                     0;
    width:                   100%;
    height:                  5px;
    border-top-right-radius: 0.2rem;
    border-top-left-radius:  0.2rem;
    background:              var(--primary-aux-color);
}

.timeline-item * {
    /* border: 1px solid black; */
    margin:     0.2rem 0;
}


.timeline-item .date {
    position:       absolute;
    display:        inline-block;
    align-self:     flex-end;
    line-height:    1.5;
    margin-right:   0.3rem;
    padding:        .20rem .5rem;
    /* border:         1px solid transparent; */
    border-radius:  .25rem;
    position:       relative;
    /* border:         1px solid #ced4da; */
    box-shadow:     rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    display:        inline-block;
    vertical-align: middle;
    margin-left:    auto;
    align-self:     center;
    font-size:      0.875rem;
    color:          var(--primary-font-color);
    opacity:        0.9;
}


/* .timeline-item .title { */
.title {
    /* border: 1px solid black; */
    position:    relative;
    color:       var(--primary-aux-color);
    font-weight: 700;
    font-size:   0.9375rem;
}

.timeline-item .title::after,.title::before {
    visibility: hidden;
}

/* pointing prism */
.timeline-item .title::after,.date::after {
    content:          '';
    position:         absolute;
    left:             calc(100% + 8px);
    top:              calc(50% - 5px);
    width:            10px;
    height:           10px;
    transform:        rotate(-135deg) skew(10deg, 10deg);
    box-shadow:       rgba(40, 40, 74, 0.25) -1px 1px 1px 0px;
    background:       var(--secondary-bg-color);
}

/* bar dot */
.timeline-item .title::before,.date::before {
    content:    '';
    position:   absolute;
    left:       calc(100% + 2.5rem - 6px);
    top:        calc(50% - 3px);
    transform:  rotate(45deg);
    width:      7px;
    height:     7px;
    background: var(--primary-aux-color);
    z-index:    99;
}


.timeline-item .location {
    color:       var(--primary-font-color);
    font-family: var(--secondary-font);
    width:       100%;
    font-size:   0.9375rem;
}

.timeline-item .description {
    color:           var(--secondary-font-color);
    font-family:     var(--secondary-font);
    width:           100%;
    font-size:       0.875rem;
    opacity:         0.8;
    font-weight:     400;
}

@media (min-width: 576px) {
    .timeline {
        display:               grid;
        grid-template-columns: 1fr 1fr;
        margin:                0;
    }
    .timeline::after {
        height: calc(100% - 3.2rem);
        left:   50%;
        top:    2.5rem;
    }
    .timeline-item {
        align-self:   start;
        margin-right: 1rem;
        grid-row:     span 2;
    }
    .timeline-item:first-child {
        grid-column: 2;
    }
    .timeline-item:nth-child(odd) {
        margin-left: 1.5rem;
    }
    .timeline-item:nth-child(even) {
        margin-right: 1.5rem;
    }
    .timeline-item:nth-child(odd) .title::after {
        left:       calc(-1rem + 3px);
        visibility: visible;
        transform:  rotate(45deg) skew(10deg, 10deg);
    }
    .timeline-item:nth-child(odd) .title::before {
        left:       calc(-2rem - 3px);
        visibility: visible;
    }
    .timeline-item:nth-child(odd) .date::before {
        visibility: hidden;
    }
    .timeline-item:nth-child(odd) .date::after {
        visibility: hidden;
    }
    .timeline-item:nth-child(even) .date::before {
        left: calc(100% + +2rem + 2px);
    }
}

.contact-form {
    margin-top: 0.6rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: calc(100% - 4rem);
    max-width: 300px;
    box-shadow: 
        rgba(50, 50, 93, 0.25) 0px 2px 10px 0px, 
        rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    border-radius: 0.4rem;
    /* padding: 1rem; */
    /* padding: 1rem 1rem 0.6rem 1rem; */
    padding: 0.7rem 1rem;
}

.contact-form * {
    margin: 0.3rem 0rem;
}

.contact-form * {
    font-family:      var(--primary-font);
    font-size:        0.875rem;
}

input, textarea {
    display:          block;
    width:            100%;
    padding:          .375rem .75rem;
    font-size:        1rem;
    line-height:      1.5;
    color:            var(--secondary-font-color);
    background-color: #fff;
    background-clip:  padding-box;
    border:           1px solid #ced4da;
    border-radius:    .25rem;
    transition:       all .15s ease-in-out;
    box-sizing:       border-box;
}

input:focus, textarea:focus {
    box-shadow: var(--primary-aux-color) 0px 0px 3px;
    outline: none;
}

textarea {
  height: 200px;
  resize:none;
}


button {
    position:        relative;
    box-sizing:      border-box;
    text-decoration: none;
    display:         inline-block;
    font-weight:     400;
    text-align:      center;
    white-space:     nowrap;
    vertical-align:  middle;
    -webkit-user-select:     none;
        -ms-user-select:     none;
            user-select:     none;
    border:          1px solid transparent;
    padding:         .375rem .75rem;
    font-size:       1rem;
    line-height:     1.5;
    border-radius:   .25rem;
    transition:      all .15s;
    color:           #fff !important;
    background:      var(--primary-aux-color);
    border-color:    var(--primary-aux-color);
    cursor:          pointer;
    outline:         none;
    background-position: center;
    transition: background 0.4s;
}

button:hover {
    color:      var(--primary-aux-color) !important;
    background: transparent !important;
}

button:after {
  content: "";
  background: transparent;
  display: block;
  position: absolute;
  width: 100%;
  height:  100%;
  border-radius:   .25rem;
  top:  0;
  left:  0;
  transform: scale(1.1);
  transition: all 0.6s;
  transform-origin: 0 100%;
}

button:active:after {
  background: var(--primary-aux-color);
  transform: scale(0);
  transition: 0s
}

button:focus, button:focus:hover {
  color:      var(--primary-aux-color) !important;
  background: transparent !important;
}

