}

.skill-item::after {
    position:   absolute;
    content:    "";
    top:        calc(100% - 3px);
    left:       0;
    width:      100%;
    background: #3FC5AE;
    background: #D9D9D9;
    height:     3px;
    z-index:    -1;
}


.skill-item .skill {
    position: relative;
    color:           var(--secondary-font-color);
    font-family:     var(--secondary-font);
    width:           100%;
    font-size:       0.875rem;
    opacity:         0.8;
