:root {
    --secondary-font:       'Poppins', sans-serif;
    --primary-font:         'Raleway', sans-serif;
    --secondary-bg-color:   #FEFDFE;
    --primary-bg-color:     #EBEDEE;
    /* --primary-font-color:   #133B5C; */
    --primary-font-color:   #25004D;
    --secondary-font-color: #422F55;
    --primary-aux-color:    #0FA08A;
    --secondary-aux-color:  #E6B184;
    --picture-size:         160px;
    font-size:              16px;
}

h2 {
    font-size: 1rem;
}

/* disable scrollbar */
html, body {
    overflow-x: hidden;
    overflow-y: scroll;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scroll-behavior: smooth;
}

body::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
}


* {
    padding: 0;
    margin: 0;
}

span, p {
    color: #422F55;
    color: var(--secondary-font-color);
    font-size: 0.875rem;
}

ul, ol {
    list-style-type: none;
}

a,
a:link,
a:visited,
a:hover,
a:active {
  text-decoration: none;
  color: #25004D;
  color: var(--primary-font-color);
}

body {
    padding:     1rem;
    width:       100%;
    box-sizing:  border-box;
    font-family: 'Raleway', sans-serif;
    font-family: var(--primary-font);
    background:  #EBEDEE;
    background:  var(--primary-bg-color);
    font-weight: 300;
}

.container {
    box-shadow:     rgba(0, 0, 0, 0.35) 0px 5px 15px;
    border-radius:  0.4rem;
    display:        flex;
    flex-direction: column;
    width:          100%;
    margin:         0 auto;
    background:     #FEFDFE;
    background:     var(--secondary-bg-color);
}

@media (min-width: 768px) {
    .container {
        width: 768px;
    }
}

@media (max-width: 576px) {
    body {
        padding: 0;
    }
    .container {
        border-radius: 0;
    }
}

